.local-upload-outer .local-upload-preview {
  border-radius: 10px;
  border: 1.6px dashed #0053FA;
  overflow: hidden;
  background: #FFFFFF;
  width: 100%;
  min-height: 150px;
  position: relative;
  margin-bottom: 24px;
}
.local-upload-outer .local-upload-preview .preview-content {
  overflow: hidden;
  width: 100%;
  position: relative;
}
.local-upload-outer .local-upload-preview .preview-content > * {
  width: 100%;
}
.local-upload-outer .local-upload-preview .local-upload-preview-close {
  position: absolute;
  z-index: 9;
  top: 0;
  right: 0px;
  width: 25px;
  height: 25px;
  padding: 8px;
  cursor: pointer;
}
.local-upload-outer .local-upload-wrapper {
  width: 100%;
}

.proof-outer .document-proof {
  display: flex;
  margin: 10px 0;
  align-items: center;
}
.proof-outer .document-proof svg {
  margin-right: 10px;
}
.proof-outer .document-proof .document-proof-remove {
  margin-left: 10px;
  padding: 10px;
  cursor: pointer;
}
.proof-outer .document-proof .document-proof-remove svg {
  color: #FF5F5F;
}

